import { requestVisit } from './../api/auth';
import { RootState } from '../../app/store';
import {
    call,
    put,
    select,
    take,
    takeEvery,
    takeLatest,
} from 'redux-saga/effects';
import axios from 'axios';
import { getMeets, setMeets, IChat, selectMeet } from './meetsSlice';
import { getAccessToken, setAccessToken } from '../auth/authSlice';

const requestMeets = (userId: number, token: string) =>
    axios.get('https://dialog.api.dc2.a4sex.net/api/contact_initials', {
        params: {
            userId,
        },
        headers: {
            Authorization: `Bearer ${token}`,
        },
    });

const readMeet = (chatId: number, token: string) =>
    axios.put(
        `https://dialog.api.dc2.a4sex.net/api/contact_initials/${chatId}`,
        { unread: false },
        {
            headers: {
                Authorization: `Bearer ${token}`,
            },
        }
    );

function* getMeestWorker(): Generator<any, any, any> {
    try {
        const { accessToken, userId } = yield select(
            (state: RootState) => state.auth
        );
        yield call(requestVisit, accessToken);
        const res = yield call(requestMeets, userId, accessToken);
        yield put(
            setMeets(
                (res.data as IChat[])
                    .filter(meet => meet.dialog)
                    .map<IChat>(meet => {
                        return {
                            ...meet,
                            human: {
                                ...meet.human,
                                name: meet.human.name ?? 'Unknown',
                                age: meet.human.age ?? 'unknown',
                                city: meet.human.city ?? 'unknown',
                            },
                        };
                    })
            )
        );
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(getAccessToken());
            yield take(setAccessToken.type);
            yield put(getMeets());
        } else console.log(error);
    }
}

interface IReadMeestWorker {
    type: string;
    payload: number;
}

function* readMeestWorker({
    payload,
}: IReadMeestWorker): Generator<any, any, any> {
    try {
        const { accessToken } = yield select((state: RootState) => state.auth);
        const { meets } = yield select((state: RootState) => state.meets);
        yield call(readMeet, meets[payload].id, accessToken);
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(getAccessToken());
            yield take(setAccessToken.type);
            yield put(selectMeet(payload));
        } else console.log(error);
    }
}

export function* meetsWatcher() {
    yield takeLatest(getMeets.type, getMeestWorker);
    yield takeEvery(selectMeet.type, readMeestWorker);
}
