import { requestVisit } from './../api/auth';
import { postMessage, getMessages, getImage } from './../api/chat';
import { RootState } from './../../app/store';
import {
    call,
    select,
    takeLeading,
    put,
    takeLatest,
    take,
} from 'redux-saga/effects';
import {
    sendMessage,
    setMessages,
    requestMessages,
    addMessage,
    IMessage,
    IImage,
    sendMessageSuccess,
} from './chatSlice';
import { getAccessToken, getCaptcha, setAccessToken } from '../auth/authSlice';

interface ISendMessageWorker {
    type: string;
    payload: {
        message: string;
        code: string;
        recipientId: number;
    };
}

interface IGetMessagesWorkerArgs {
    type: string;
    payload: number;
}

function* sendMessageWorker({
    payload,
}: ISendMessageWorker): Generator<any, any, any> {
    try {
        const { refreshToken, accessToken, captcha } = yield select(
            (state: RootState) => state.auth
        );

        yield call(requestVisit, accessToken);

        const res = yield call(
            postMessage,
            payload.recipientId,
            payload.message,
            { code: payload.code, token: captcha.token },
            refreshToken
        );

        if (res.data.error && res.data.error === 'need_captcha') {
            yield put(getCaptcha());
        } else {
            yield put(
                addMessage({
                    ...res.data.message,
                    message: res.data.message.mess,
                    added: res.data.message.date,
                })
            );
            yield put(sendMessageSuccess());
        }
    } catch (error) {
        console.log(error);
    }
}

function* getMessagesWorker({
    payload,
}: IGetMessagesWorkerArgs): Generator<any, any, any> {
    try {
        const { accessToken, userId } = yield select(
            (state: RootState) => state.auth
        );
        const res = yield call(getMessages, userId, +payload, accessToken);
        const messages: IMessage[] = [];
        // const images: IImage[] = [];

        for (const message of res.data) {
            // if (lastMessageId && message.id === lastMessageId) break;
            const [prefix, alias] = message.message
                .replace(/^\[\[|\]\]$/g, '')
                .split(':');
            if (prefix !== 'IMG') {
                message.isImg = false;
                messages.push({ ...message, isImg: false });
                continue;
            }
            try {
                const res = yield call(
                    getImage,
                    alias,
                    message.fromId,
                    accessToken
                );

                const [photo, src]: [photo: IImage, src: string] = [
                    res.data.photo,
                    res.data.photo._links.origin.href,
                ];
                // images.push({
                //     id: photo.id as string,
                //     src,
                //     alt: alias,
                //     sender: message.fromId,
                // });
                messages.push({ ...message, isImg: true, src });
                // yield put(incGalleryCounter());
            } catch (error) {
                console.log(error);
                messages.push({
                    ...message,
                    isImg: false,
                    message: '[[IMAGE NOT FOUND]]',
                });
            }
        }
        yield put(setMessages(messages));
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(getAccessToken());
            yield take(setAccessToken.type);
            yield put(requestMessages(payload));
        } else console.log(error);
    }
}

export function* chatWatcher() {
    yield takeLatest(requestMessages.type, getMessagesWorker);
    yield takeLeading(sendMessage.type, sendMessageWorker);
}
